.gpt3__posobility{
    display: flex;
    flex-direction: row;
}

.gpt3__posobility-image{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-right: 2rem;
}

.gpt3__posobility-image img{
    width: 100%;
    height: 100%;
}

.gpt3__posobility-content{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
}

.gpt3__posobility-content h4{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #71E5FF;
}
.gpt3__posobility-content h4:last-child{
    color: #FF8A71;


}
.gpt3__posobility-content h1{
    font-family:var(--font-family) ;
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
    margin: 3rem 0;
}
.gpt3__posobility-content p{
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 30px;


    color: var(--color-text);
    margin-bottom: 2rem;
}    

@media screen and(max-width:950px) {
    .gpt3__posobility{
        flex-direction: column;
    }

    .gpt3__posobility-image{
        margin: 1rem 0;
    }
    .gpt3__posobility-content{
        margin-top: 2rem;
    }
    
}